const actions = {
  getDeliveryOrders: (page, search, filter) => ({
    type: "GET_DELIVERY_ORDERS_REQUEST",
    payload: {
      page: page,
      search: search,
      filter: filter
    }
  }),
  createDeliveryOrder: () => ({
    type: "CREATE_DELIVERY_ORDER_REQUEST"
  }),
  getTransactions: (page, search, filter) => ({
    type: "GET_FILTER_SO_DELIVERY_ORDER_REQUEST",
    payload: {
      page: page,
      search: search,
      filter: filter
    }
  }),
  storeDeliveryOrder: data => ({
    type: "STORE_DELIVERY_ORDER_REQUEST",
    payload: { body: data }
  }),
  editDeliveryOrder: (id_delivery_order, search) => ({
    type: "EDIT_DELIVERY_ORDER_REQUEST",
    payload: { id_delivery_order: id_delivery_order, search: search }
  }),
  updateDeliveryOrder: (id_delivery_order, data) => ({
    type: "UPDATE_DELIVERY_ORDER_REQUEST",
    payload: {
      id_delivery_order: id_delivery_order,
      body: data
    }
  }),
  showDeliveryOrder: id_delivery_order => ({
    type: "SHOW_DELIVERY_ORDER_REQUEST",
    payload: { id_delivery_order: id_delivery_order }
  }),
  confirmDeliveryOrder: id_delivery_order => ({
    type: "CONFIRM_DELIVERY_ORDER_REQUEST",
    payload: { id_delivery_order }
  }),
  rejectDeliveryOrder: (id_delivery_order, data) => ({
    type: "REJECT_DELIVERY_ORDER_REQUEST",
    payload: { id_delivery_order, body: data }
  }),
  cancelDeliveryOrder: (id_delivery_order, data) => ({
    type: "CANCEL_DELIVERY_ORDER_REQUEST",
    payload: { id_delivery_order, body: data }
  }),
  // attachment management
  uploadDocumentDO: (id_delivery_order, data) => ({
    type: "UPLOAD_DELIVERY_ORDER_DOCUMENT_REQUEST",
    payload: {
      id_delivery_order,
      body: data
    }
  }),
  downloadDocumentDO: id_delivery_order_document => ({
    type: "DOWNLOAD_DELIVERY_ORDER_DOCUMENT_REQUEST",
    payload: { id_delivery_order_document }
  }),
  deleteDocumentDO: (id_delivery_order_document, id_delivery_order) => ({
    type: "DELETE_DELIVERY_ORDER_DOCUMENT_REQUEST",
    payload: {
      id_delivery_order_document: id_delivery_order_document,
      id_delivery_order: id_delivery_order
    }
  }),
  setPrintData: data => ({
    type: "SET_PRINT_DATA_DELIVERY_ORDER",
    payload: { body: data }
  }),
  clearSuccess: () => ({
    type: "CLEAR_SUCCESS"
  }),
  clearError: () => ({
    type: "CLEAR_ERROR"
  }),
  clearErrorField: () => ({
    type: "CLEAR_ERROR_FIELD"
  })
};

export default actions;
