import React, { Component } from "react";
import { Form, InputNumber, Tooltip } from "antd";
import EditableContext from "./editableContext";
import IntlMessages from "../../../../components/utility/intlMessages";
import NumberFormat from "../../../App/NumberFormat";

const FormItem = Form.Item;

class EditableCell extends Component {
  state = {
    editing: false,
    err: null
  };

  toggleEdit = () => {
    const editing = !this.state.editing;

    this.setState({ editing }, () => {
      if (editing) {
        this.input.focus();
      }
    });
  };

  handleClickOutside = e => {
    const { editing } = this.state;

    if (editing && !this.cell) {this.toggleEdit();} else if (editing && this.cell !== e.target && !this.cell.contains(e.target)) {
      this.save();
    }
  };

  save = () => {
    const { record, handleSave } = this.props;

    this.form.validateFields((error, values) => {
      values.left_qty = (
        parseFloat(record.ordered_qty) -
        parseFloat(record.delivered_qty) -
        parseFloat(values.delivery_qty)
      ).toFixed(2);
      values.delivery_qty = parseFloat(values.delivery_qty).toFixed(2);

      if (parseFloat(values.left_qty) < 0) {
        this.setState({ err: <IntlMessages id="lessOrEqualSOQty" /> });
        return;
      }
      if (error) {
        return;
      }
      this.setState({ err: null });
      this.toggleEdit();

      document.removeEventListener("click", this.handleClickOutside, true);
      handleSave({ ...record, ...values });
    });
  };

  render() {
    const { editing, err } = this.state;
    const {
      editable,
      dataIndex,
      title,
      record,
      lock,
      index,
      handleSave,
      taxes,
      ...restProps
    } = this.props;
    return (
      <td ref={node => (this.cell = node)} {...restProps}>
        {editable && !lock ? (
          <EditableContext.Consumer>
            {form => {
              this.form = form;
              if (editing) {
                document.addEventListener(
                  "click",
                  this.handleClickOutside,
                  true
                );
                return (
                  <FormItem
                    style={{ margin: 0 }}
                    validateStatus={
                      err || form.getFieldError(dataIndex) ? "error" : null
                    }
                    help={err ? err : form.getFieldError(dataIndex)}
                  >
                    {form.getFieldDecorator(dataIndex, {
                      rules: [
                        {
                          required: true,
                          message: <IntlMessages id="required" />
                        }
                      ],
                      initialValue: record[dataIndex]
                    })(
                      <InputNumber
                        ref={node => (this.input = node)}
                        onPressEnter={this.save}
                        formatter={value =>
                          `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                        }
                        parser={value => value.replace(/\$\s?|(,*)/g, "")}
                        min={1}
                        precision={0}
                      />
                    )}
                  </FormItem>
                );
              } else if (!editing && dataIndex === "delivery_qty") {
                return (
                  <>
                    <div
                      className="editable-cell-value-wrap"
                      // style={{ paddingRight: 24 }}
                      onClick={this.toggleEdit}
                    >
                      {record[dataIndex] 
                        ? <NumberFormat value={record[dataIndex]} />
                        : "0"}
                    </div>
                    {record.current_qty && isNaN(parseFloat(record.current_qty))
                        ? (
                        // item grouping insufficient 'qty'
                        <span style={{ color: "orange", fontSize: ".9em" }}>
                          {parseFloat(record.current_qty) === 0 ? (
                            ""
                          ) : (
                            <Tooltip
                              placement="bottom"
                              title={
                                <>
                                  {record.current_qty.search(":") >= 0 ? (
                                    <NumberFormat 
                                      prefix={`${record.current_qty.slice(0, record.current_qty.search(":"))}: `}
                                      value={record.current_qty.slice(record.current_qty.search(":") + 1)}
                                    />
                                  ) : record.current_qty}
                                </>
                              }
                            >
                              {record.current_qty.substring(0, 9) + "..."}
                            </Tooltip>
                          )}
                        </span>
                      ) : null}
                  </>
                );
              } else {
                return (
                  <div
                    className="editable-cell-value-wrap"
                    // style={{ paddingRight: 24 }}
                    onClick={this.toggleEdit}
                  >
                    {restProps.children}
                  </div>
                );
              }
            }}
          </EditableContext.Consumer>
        ) : (
          restProps.children
        )}
      </td>
    );
  }
}

export default EditableCell;
