import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";

import DeliveryOrderCreateForm from "./createForm";

import notification from "../../../../components/notification";
// import { Input, Select, Divider } from "antd";

import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/sales/deliveryOrder/actions";
import { Box } from "../../../App/app.style";
import { removeSaveData } from "../../../../helpers/helper";

const redirecTo = "/dashboard/sales/delivery-orders";

class CreateDeliveryOrder extends Component {
  componentDidMount() {
    const { createDeliveryOrder } = this.props;
    createDeliveryOrder();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.deliveryOrder.error) {
      notification("error", this.props.deliveryOrder.error);
      this.props.clearError();
    }
    if (
      this.props.deliveryOrder.success &&
      this.props.deliveryOrder.id_delivery_order
    ) {
      this.props.history.replace(
        redirecTo + "/update/" + this.props.deliveryOrder.id_delivery_order
      );
    }
  }

  handleSubmit = value => {
    const { storeDeliveryOrder } = this.props;
    storeDeliveryOrder(value);
  };

  render() {
    // console.log(this.props);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader title={<IntlMessages id="deliveryOrders.add" />}>
            <Button
              onClick={() => {
                removeSaveData("do");
                this.props.history.replace(redirecTo);
              }}
            >
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          <DeliveryOrderCreateForm
            clearError={this.props.clearError}
            deliveryOrder={this.props.deliveryOrder}
            submit={this.handleSubmit}
          />
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    deliveryOrder: state.deliveryOrder,
    auth: state.auth
  };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(CreateDeliveryOrder);
